body {
  background-color: #ecf0f1;
  font-family: 'Laila', serif;
  font-size: 18px;
  padding: 0%;
  margin: 0%;
}

h1, h3 {
  font-weight: 700;
}

p, h3 {
  line-height: 22px;
}


.Header {
  margin: 15px 0;
  position: relative; }
  .Header hr.hr {
    border-top: 2px solid #95a5a6;
    border-bottom: 1px solid #95a5a6; }
  .Header h1 {
    font-size: 24px; }
  .Header--contact {
    font-weight: 700; }
    .Header--contact a {
      font-weight: 500;
      color: #989595;
      text-decoration: none; }
  .Header--profile-picture {
    height: 160px;
    -webkit-transition: -webkit-transform 0.2s ease-in;
    transition: -webkit-transform 0.2s ease-in;
    transition: transform 0.2s ease-in;
    transition: transform 0.2s ease-in, -webkit-transform 0.2s ease-in;
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
    position: absolute;
    top: -5px;
    right: -5px; }
    .Header--profile-picture:hover {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg); }
  .Header--summary {
    margin-top: 20px; }
  .Header--language-select {
    padding-top: 10px;
    position: absolute;
    top: -25px;
    left: 0; }

main .Section {
  display: -webkit-flex;
  display: flex;
  min-height: 100px;
  padding: 10px 0; }
  main .Section--left {
    -webkit-flex: 2 1;
            flex: 2 1;
    padding: 0 20px;
    position: relative; }
    main .Section--left h2,
    main .Section--left h3 {
      font-style: italic;
      text-align: right; }
    main .Section--left:after {
      content: '';
      height: 100px;
      width: 1px;
      position: absolute;
      top: 0;
      right: 0;
      background: #95a5a6;
      border: 1px solid #95a5a6; }
  main .Section--right {
    padding: 4px 20px;
    -webkit-flex: 9 1;
            flex: 9 1; }
    main .Section--right-job-title {
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: space-between;
              justify-content: space-between; }
      main .Section--right-job-title span {
        font-size: 14px;
        color: #989595;
        opacity: 0.75;
        font-style: italic;
        padding-left: 35px;
        line-height: 22px; }
    main .Section--right p span {
      font-weight: 700; }
    main .Section--right div {
      padding-bottom: 15px; }
      main .Section--right div:nth-last-child(1) {
        padding-bottom: 0; }
    main .Section--right ul {
      list-style: disc inside;
      margin: 0%;
      padding: 0%; }

.Footer {
  line-height: normal;
  line-height: initial;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  background: #95a5a6; }
  .Footer svg {
    padding: 10px;
    font-size: 24px;
    color: #e1e1e1;
    -webkit-transition: color 0.3s linear;
    transition: color 0.3s linear; }
    .Footer svg:hover {
      color: #ffffff; }

.App {
  max-width: 1024px;
  margin: 0 auto;
  padding: 3%; }

@media screen and (max-width: 767px) {
  .Section {
    -webkit-flex-direction: column;
            flex-direction: column; }
    .Section--left h2,
    .Section--left h3 {
      text-align: left !important;
      width: 100%;
      border-bottom: 2px solid #95a5a6; }
    .Section--left:after {
      display: none; }
    .Section--left--column {
      width: 120px; }
  .Header--profile-picture {
    height: 120px; } }

